import React, { Component } from "react";
import "../css/base.css";
import { Link } from "react-router-dom";

class Header extends Component {
  render() {
    return (
      <header className="header-section mt-5">
        <section className="header-section__content mx-5 lg:m-0">
          <div className="flex flex-wrap border-b">
            <div className="flex flex-wrap w-full sm:w-1/2 md:w-2/3 lg:w-1/3 xl:w-1/3 ml-auto">
              <div className="header-section__logo float-left">
                <h4 className="mb-0 text-left name-text">Michael Sarpong</h4>
                <p className="pt-0 job-title-text">
                  UI/UX Designer | Frontend Developer
                </p>
              </div>
            </div>
            <div className="w-full sm:w-1/2 md:w-2/3 lg:w-1/3 xl:w-1/3 mr-auto">
              <div className="mobile-nav visible sm:invisible md:visible lg:visible xl:visible lg:float-right pt-8">
                <ul className="list-none">
                  <li>
                    <Link to="/">Home</Link>
                  </li>
                  <li>
                    <Link to="/about">About</Link>
                  </li>
                  <li>
                    <Link to="/works">Works</Link>
                  </li>
                  <li>
                    <Link to="/writing">Writing</Link>
                  </li>
                  <li>
                    <Link to="/contact">Contact</Link>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </section>
      </header>
    );
  }
}

export default Header;
